/* Basic styles for the image */
.rounded-image {
    transition: transform 0.1s ease; /* Smooth transition for movement */
    cursor: pointer; /* Change cursor to pointer to indicate it's clickable */
    display: block; /* Ensure the image is displayed as a block element */
}

/* Move the image forward (downwards) */
.move-forward {
    transform: translateY(50px); /* Move the image down by 50px */
}

/* Move the image back to its original position */
.move-back {
    transform: translateY(0); /* Move the image back to its original position */
}
/* CSS classes to control the visibility of the paragraph */
.info-visible {
    visibility: visible; /* Make the paragraph visible */
    opacity: 1; /* Ensure the text is fully opaque */
    transition: opacity 0.5s ease; /* Smooth transition for opacity */
    color: white;
}

.info-hidden {
    visibility: hidden; /* Hide the paragraph */
    opacity: 0; /* Make the text fully transparent */
    transition: opacity 0.5s ease; /* Smooth transition for opacity */
}